.appointment-card {
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
  padding: 12px 16px;
  margin-bottom: 12px;
}

.card-content h4 {
  font-size: 14px;
  margin: 0 0 6px;
  color: #333;
}

.card-content span {
  font-size: 13px;
  color: #888;
}
